html {
  height: 100%;
  box-sizing: border-box;
  font-size: 62.5%; }

*, *::before, *::after {
  box-sizing: inherit;
  outline: 0.1rem dashed lightgrey; }

body {
  overflow-x: hidden;
  width: 100%;
  max-width: 100%;
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  font-variant-numeric: proportional-nums;
  font-feature-settings: "pnum";
  font-size: 1.4rem; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }
  a:hover, a:active {
    text-decoration: none; }

ul {
  padding: 0;
  list-style-type: none; }



